/*
* Style Works for Editor Only
*/

.wp-block-flexguten-amazon-review-one {
	border: 1px solid #ccc;
	padding: 20px;
	.amazon-review-block {
		display: grid;
		grid-template-columns: 2fr 3fr;
		grid-gap: 20px;
		.review-image {
			height: auto;
			width: auto;
			display: grid;
			align-content: center;
			& img {
				object-fit: cover;
			}
		}

		.star-rating {
			font-size: 25px;
		}

		.review-text {
			padding: 0px 15px;

			& h3 {
				margin: 0px;
				font-family: "Nunito", sans-serif;
				font-weight: 800;
			}

			.product-features {
				padding: 15px;
			}
		}

		.flexguten-pricing-section {
			justify-items: end;
			display: grid;
		}

		p.block-editor-rich-text__editable.rich-text {
			margin: 0px;
		}
	}
}

.my-tab-panel {
	border-top: 1px solid #ccc;

	.scb-heading {
		margin-bottom: 16px;
		font-weight: 500;
	}

	.components-tab-panel__tabs-item:focus:not(:disabled) {
		box-shadow: none;
	}

	.components-panel__body-toggle.components-button:focus {
		box-shadow: none;
	}

	.components-button:focus:not(:disabled) {
		box-shadow: none;
	}

	.flexguten-general {
		display: block;
		width: 100%;
	}

	.flexguten-gap {
		margin-top: 20px;
	}

	.flexguten-general.active-tab {
		background: #ff99009e;
		color: #29434a;
		border-top: 3px solid #ff9900;
	}

	.new-feature-list {
		margin: 10px 0px 40px 0px;
		.new-feature-button {
			float: right;
			margin: 10px;
			& button {
				background: #007cba;
				border: none;
				padding: 7px 15px;
				color: white;
				border-radius: 5px;
				cursor: pointer;
			}
		}

		.all-features {
			display: grid;
			grid-template-columns: 5fr 1fr;
			.feature-remove {
				width: 100%;
				background: none;
				border: none;
				cursor: pointer;
				& svg {
					fill: #aa1212;
					margin-top: -5px;
					height: 40px;
				}
			}
		}
	}

	.flexguten-label-spacing {
		margin-top: 70px;
	}
}
